.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    margin: auto;
    position: fixed;
    width: 100%;
    height: 100px;
    z-index: 10;
    
}

.header-bg{
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}

.nav-menu{
    display: flex;
    gap: 40px;
}

.nav-menu li{
    padding: 0 1rem;
}

.nav-menu li a{
    font-size: 1.3rem;
    font-weight: 500;
}


.hamburger{
   display: none; 
}

@media screen and (max-width: 1040px) {
    .nav-menu{
       flex-direction: column; 
       justify-content: center;
       align-items: center;
       width: 100%;
       height: 100vh;
       background: rgba(0,0,0,0.9);
       position: absolute;
       top: 0;
       left: -100%;
       z-index: -4;
       transition: 0.5s;
    }

    .nav-menu.hip{
        left: 0;
    }

    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial; 
     }
}