.hero-img{
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    height: 60vh;
    position: relative;

}

.hero-img::before{
    content: " ";
    background: url("https://images.unsplash.com/photo-1537432376769-00f5c2f4c8d2?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1025&q=80");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
    opacity: 0.4;

}

.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1{
    font-size: 3rem;
    text-transform: uppercase;
}

.hero-img p{
    width: 50%;
    font-size: 1.4rem;
    text-align: center;
    margin-top: 2rem;
    line-height: 2rem;
}


@media screen and (max-width:640) {
    .hero-img h1{
        font-size: 2rem;
    }
}